// The module 'vscode' contains the VS Code extensibility API
// Import the module and reference it with the alias vscode in your code below
import * as vscode from 'vscode';
import axios from 'axios';

// Function to fetch code suggestions from Grok.com
async function fetchGrokSuggestions(context: string): Promise<string[]> {
    try {
        const response = await axios.post('https://api.grok.com/suggestions', {
            context,
        });
        return response.data.suggestions;
    } catch (error) {
        console.error('Error fetching suggestions from Grok:', error);
        return [];
    }
}

// Function to fetch responses from Grok.com
async function fetchGrokResponse(query: string): Promise<string> {
    try {
        const response = await axios.post('https://api.grok.com/chat', {
            query,
        });
        return response.data.response;
    } catch (error) {
        console.error('Error fetching response from Grok:', error);
        return 'Error: Unable to fetch response from Grok.';
    }
}

// Function to create and show the chat webview
function createChatWebview(context: vscode.ExtensionContext) {
    const panel = vscode.window.createWebviewPanel(
        'grokChat',
        'Grok Assistant Chat',
        vscode.ViewColumn.One,
        {
            enableScripts: true,
        }
    );

    panel.webview.html = getWebviewContent();

    // Handle messages from the webview
    panel.webview.onDidReceiveMessage(async (message) => {
        if (message.command === 'query') {
            const response = await fetchGrokResponse(message.text);
            panel.webview.postMessage({ command: 'response', text: response });
        }
    });
}

// Function to get the HTML content for the webview
function getWebviewContent(): string {
    return `
        <!DOCTYPE html>
        <html lang="en">
        <head>
            <meta charset="UTF-8">
            <meta name="viewport" content="width=device-width, initial-scale=1.0">
            <title>Grok Assistant Chat</title>
            <style>
                body { font-family: Arial, sans-serif; margin: 0; padding: 0; }
                #chat { height: 80vh; overflow-y: auto; padding: 10px; border: 1px solid #ccc; }
                #input { width: 100%; padding: 10px; }
            </style>
        </head>
        <body>
            <div id="chat"></div>
            <input id="input" type="text" placeholder="Type your query here..." />
            <script>
                const vscode = acquireVsCodeApi();
                const chat = document.getElementById('chat');
                const input = document.getElementById('input');

                input.addEventListener('keydown', (e) => {
                    if (e.key === 'Enter') {
                        const text = input.value;
                        if (text) {
                            chat.innerHTML += \`<div><strong>You:</strong> \${text}</div>\`;
                            vscode.postMessage({ command: 'query', text });
                            input.value = '';
                        }
                    }
                });

                window.addEventListener('message', (event) => {
                    const message = event.data;
                    if (message.command === 'response') {
                        chat.innerHTML += \`<div><strong>Grok:</strong> \${message.text}</div>\`;
                    }
                });
            </script>
        </body>
        </html>
    `;
}

// This method is called when your extension is activated
// Your extension is activated the very first time the command is executed
export function activate(context: vscode.ExtensionContext) {
    console.log('Grok Assistant is now active!');

    const disposable = vscode.commands.registerCommand('grok-assistant.getSuggestions', async () => {
        const editor = vscode.window.activeTextEditor;
        if (!editor) {
            vscode.window.showErrorMessage('No active editor found.');
            return;
        }

        const document = editor.document;
        const selection = editor.selection;
        const selectedText = document.getText(selection);

        const suggestions = await fetchGrokSuggestions(selectedText);

        if (suggestions.length > 0) {
            vscode.window.showQuickPick(suggestions, {
                placeHolder: 'Select a suggestion to insert',
            }).then((selected) => {
                if (selected) {
                    editor.edit((editBuilder) => {
                        editBuilder.replace(selection, selected);
                    });
                }
            });
        } else {
            vscode.window.showInformationMessage('No suggestions available.');
        }
    });

    context.subscriptions.push(disposable);

    const chatDisposable = vscode.commands.registerCommand('grok-assistant.openChat', () => {
        createChatWebview(context);
    });

    context.subscriptions.push(chatDisposable);
}

// This method is called when your extension is deactivated
export function deactivate() {}
